$dark_color: rgb(10, 50, 130);
$light_color: rgb(200, 120, 0);

$dots_row: 20; //количество точек в строке/столбце квадрата
$bw: .2%; //для расстояний между точками
$wh: 100/$dots_row - $bw; //подсчёт процентного составляющего значения актива


//////input reset/////////////////////////////////
input, button, select {
	outline: 0;
	border: none; }

//////////////////////////////////////////////

body {
	background-color: rgb(25, 82, 138);
	font-family: arial;
	font-weight: bold;
	text-align: center;
	transition-property: background-color;
	transition-duration: 0.8s; }

header {
	display: flex;
	padding-top: 2%;
	justify-content: center;
	display: none; }


//основной контейнер для позиционирования квадрата с активной областью

#container {
	display: flex;
	justify-content: space-around;
	padding-top: 2%;
	#center { //хитрый
		width: 520px;
		font-size: 18px;
		.center { //способ
			display: flex;
			justify-content: space-between;
			width: 99%;
			.box_wraper { //сделать адаптивный по ширине квадрат
				overflow: hidden;
				position: relative;
				width: 100%;
				padding-bottom: 100%;
				.box { //квадрат содержащий активные области
					background-color: black;
					border: 1px solid white;
					display: flex;
					flex-wrap: wrap;
					justify-content: space-around;
					transition-duration: .6s;
					width: 99%;
					height: 99%;
					margin: auto;
					position: absolute;
					top: 0;
					left: 0;
					overflow: hidden;
					.dot { //активные пиксели
						border-radius: 8px;
						background-color: $dark_color;
						color: $light_color;
						line-height: 130%;
						height: $wh;
						width: $wh; }
					.dot.wall { //пиксели интерфейса
						background-color: $light_color; } } } } } }

.left, .right {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center; }


.after {
	height: 100px;
	display: none;
	justify-content: center;
	align-items: center; }

#start, #pause, #left, #right, #flip, #down, .start, .pause {
	border-radius: 5px;
	color: $light_color;
	cursor: pointer;
	font-weight: bold;
	padding: 10px;
	margin: 2px 10px;
	width: 80px; }


#left, #right, #flip, #down, .start, .pause {
	height: 80px;
	border-radius: 50%;
	line-height: 300%;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 10px; }

#start, #pause {
	width: 100px; }

.start, .pause {
	margin-top: 15px;
	height: 15px;
	width: 15px; }

.down {
	display: flex;
	flex-direction: column;
	align-items: center; }

#down {
	background: url("../img/down.png") no-repeat center center $dark_color;
	background-size: 30%; }

#left, #right {
	background: url("../img/c-down.png") no-repeat center 55% $dark_color;
	background-size: 30%; }

#left {
	transform: rotate(90deg); }

#right {
	transform: rotate(-90deg); }

#flip {
	background: url("../img/refresh2.png") no-repeat center center $dark_color;
	background-size: 30%; }

.start {
	background-color: $dark_color; }

.start div {
	height: 7px;
	width: 7px;
	border-radius: 50%;
	background-color: whitesmoke; }

.pause {
	background: url("../img/pause.png") center center no-repeat $dark_color;
	background-size: 40%; }

#down, #flip {
	height: 55px;
	width: 55px; }

#start:hover, #pause:hover {

	background-color: $light_color;
	color: $dark_color; }

footer {
	display: none; }

/////////////////////////БЛОК МЕДИА-ЗАПРОСОВ///////////////////////////

@media (min-width: 1024px) {//исчезновение/появления ненужных для телефона блоков
	#left, #right, #flip, #down, .start, .pause {
		display: none; }
	footer {
		display: block; }
	header {
		display: block; }
	.after {
		display: flex; } }


@media screen and (max-width: 500px) { //уменьшение размера активных кнопок на малом экране
	#left, #right, #flip, #down {
		height: 50px;
		width: 50px; } }

/////способ сохранить квадрат по высоте////
@media screen and (max-height: 500px) {
	#container #center {
		width: 90vh;
		font-size: 3vh; } }

